.online-education-top-wrapper {
  display: flex;
  width: 90vw;
  margin: 4rem auto;
  flex-direction: row-reverse;
  flex-wrap: wrap;
  justify-content: space-evenly;
  background-color: #f9f9f9;
  box-shadow: 20px 20px 50px 10px rgb(182, 181, 181);
  padding: 2rem 0;
}

.online-education-right-element {
  width: 40%;
  text-align: end;
}

@media (max-width: 600px) {
  .online-education-right-element {
    width: 90%;
  }
}

