.online-education-bottom-element {
  display: block;
  text-align: center;
  width: 90vw;
  margin: 4rem auto;
  background-color: #f9f9f9;
  box-shadow: 20px 20px 50px 10px rgb(182, 181, 181);
  padding: 2rem 0;
}
